import React from 'react'
import ReactDOM from 'react-dom'
import {createStore, applyMiddleware} from 'redux'
import {composeWithDevTools} from 'redux-devtools-extension'
import thunk from 'redux-thunk'
import {syncHistoryWithStore} from 'react-router-redux'
import {Router, Route, browserHistory} from 'react-router'
import {Provider} from 'react-redux'
import './main.css'
import reducers from './reducers/index.js'
import Layout from './containers/layout/index.js'
// import Phone from './containers/phone/index.js'
import Phones from './containers/phones/index.js'
// import Basket from './containers/basket/index.js'
// import Order from './components/order/index.js'
import * as serviceWorker from './serviceWorker.js'
const store = createStore(reducers, composeWithDevTools(
    applyMiddleware(thunk)
))

const history = syncHistoryWithStore(browserHistory, store)

ReactDOM.render(
    <Provider store={store}>
        <Router history={history}>
            <Route component={Layout}>
                <Route path='/' /*component={Phones}*/ />
                {/*<Route path='/categories/:id' component={Phones}/>*/}
            </Route>
            {/*<Route path='/order' component={Order}/>*/}
            {/*<Route path='/phones/:id' component={Phone} />*/}
            {/*<Route path='/basket' component ={Basket}/>*/}
        </Router>
    </Provider>,
    document.getElementById('root')
);
serviceWorker.unregister();
